<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-适应文本高度的文本域1">适应文本高度的文本域</a></li><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-复合型输入框1">复合型输入框</a></li><li><a href="#vue-doc-尺寸1">尺寸</a></li><li><a href="#vue-doc-可清空1">可清空</a></li><li><a href="#vue-doc-带Icon的输入框1">带Icon的输入框</a></li><li><a href="#vue-doc-文本域1">文本域</a></li><li><a href="#vue-doc-禁用状态1">禁用状态</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-input输入框1" class="vue-doc-element">Input 输入框</h1>
<p class="vue-doc-element">基本表单组件，支持 input 和 textarea，并在原生控件基础上进行了功能扩展，可以组合使用。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demoautoHight></demoautoHight>
<demobasic></demobasic>
<demoslot></demoslot>
<demosize></demosize>
<democlearable></democlearable>
<demoicon></demoicon>
<demotextarea></demotextarea>
<demodiabled></demodiabled>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-inputprops1_1" class="vue-doc-element">Input props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>输入框类型，可选值为 text、password、textarea、url、email、date</td>
<td>String</td>
<td>text</td>
</tr>
<tr>
<td>value</td>
<td>绑定的值，可使用 v-model 双向绑定</td>
<td>String / Number</td>
<td>空</td>
</tr>
<tr>
<td>size</td>
<td>输入框尺寸，可选值为large、small、default或者不设置</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位文本</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>clearable</td>
<td>是否显示清空按钮</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>设置输入框为禁用状态</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>readonly</td>
<td>设置输入框为只读</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>maxlength</td>
<td>最大输入长度</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>icon</td>
<td>输入框尾部图标，仅在 text 类型下有效</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>rows</td>
<td>文本域默认行数，仅在 textarea 类型下有效</td>
<td>Number</td>
<td>2</td>
</tr>
<tr>
<td>autosize</td>
<td>自适应内容高度，仅在 textarea 类型下有效，可传入对象，如 { minRows: 2, maxRows: 6 }</td>
<td>Boolean / Object</td>
<td>false</td>
</tr>
<tr>
<td>number</td>
<td>将用户的输入转换为 Number 类型</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>autofocus</td>
<td>自动获取焦点</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>autocomplete</td>
<td>原生的自动完成功能，可选值为 off 和 on</td>
<td>String</td>
<td>off</td>
</tr>
<tr>
<td>element-id</td>
<td>给表单元素设置 id，详见 Form 用法。</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>spellcheck</td>
<td>原生的 spellcheck 属性</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>wrap</td>
<td>原生的 wrap 属性，可选值为 hard 和 soft，仅在 textarea 下生效</td>
<td>String</td>
<td>soft</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-inputevents1_2" class="vue-doc-element">Input events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-enter</td>
<td>按下回车键时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-click</td>
<td>设置 icon 属性后，点击图标时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-change</td>
<td>数据改变时触发</td>
<td>event</td>
</tr>
<tr>
<td>on-focus</td>
<td>输入框聚焦时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-blur</td>
<td>输入框失去焦点时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-keyup</td>
<td>原生的 keyup 事件</td>
<td>event</td>
</tr>
<tr>
<td>on-keydown</td>
<td>原生的 keydown 事件</td>
<td>event</td>
</tr>
<tr>
<td>on-keypress</td>
<td>原生的 keypress 事件</td>
<td>event</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-inputslot1_3" class="vue-doc-element">Input slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>prepend</td>
<td>前置内容，仅在 text 类型下有效</td>
</tr>
<tr>
<td>append</td>
<td>后置内容，仅在 text 类型下有效</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-inputmethods1_4" class="vue-doc-element">Input methods</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>focus</td>
<td>手动聚焦输入框</td>
<td>无</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demoautoHight from './demo/autoHight.md';
import demobasic from './demo/basic.md';
import demoslot from './demo/slot.md';
import demosize from './demo/size.md';
import democlearable from './demo/clearable.md';
import demoicon from './demo/icon.md';
import demotextarea from './demo/textarea.md';
import demodiabled from './demo/diabled.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demoautoHight,demobasic,demoslot,demosize,democlearable,demoicon,demotextarea,demodiabled
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


